@font-face {
  font-family: "Bebas";
  font-display: swap;
  src: url("../../fonts/BebasNeue-Regular.woff2") format("woff2"), url("../../fonts/BebasNeue-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "PTSans";
  font-display: swap;
  src: url("../../fonts/PTSans-Regular.woff2") format("woff2"), url("../../fonts/PTSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Akziden";
  font-display: swap;
  src: url("../../fonts/AkzidenzGroteskBQCondensed-Bold.woff2") format("woff2"), url("../../fonts/AkzidenzGroteskBQCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

/*stylelint-disable*/

html {
  -webkit-text-size-adjust: 100%;
}

hr {
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: inherit;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}

small {
  font-size: inherit;
}

sub,
sup {
  font-size: inherit;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

main,
nav,
footer,
header,
aside {
  display: block;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}

.container {
  max-width: 910px;
  margin: 0 auto;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

html,
body {
  font-family: "Akziden", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 25px;
  color: #000000;
  height: 100%;
  width: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

img {
  vertical-align: top;
  border-style: none;
  max-width: 100%;
  height: auto;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
}

.page {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
}

.content {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
}

body.lock {
  overflow: hidden;
}

.main-header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  right: 0;
  min-height: 175px;
  padding-top: 29px;
  padding-bottom: 30px;
}

.main-header::before {
  position: absolute;
  content: "";
  width: 100%;
  min-height: 100px;
  top: 0;
  left: 0;
  background-color: transparent;
  z-index: -1;
}

.main-header .container {
  display: flex;
  flex-direction: column;
}

.main-header__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.main-header__burger {
  display: none;
}

.main-header__btn {
  width: 20px;
  height: 20px;
  padding: 0;
  font-family: "Bebas", "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 10px;
  color: #000000;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  text-decoration: underline;
  transition: all 0.5s ease 0s;
}

.main-header__btn.active {
  color: #ffffff;
  background-color: #000000;
  text-decoration: none;
}

.main-header__btn:hover,
.main-header__btn:focus {
  outline: none;
  color: #ffffff;
  background-color: #000000;
  text-decoration: none;
  transition: all 0.5s ease 0s;
}

.main-header__btn:active {
  color: rgba(255, 255, 255, 0.8);
  background-color: rgba(0, 0, 0, 0.4);
  transition: all 0.1s ease 0s;
}

.main-header__link {
  font-size: 15px;
  color: #000000;
  text-transform: uppercase;
  transition: all 0.5s ease 0s;
}

.main-header__link:hover,
.main-header__link:focus {
  outline: none;
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
}

.main-header__link:active {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.1s ease 0s;
  text-decoration: none;
}

.main-header__logo {
  position: absolute;
  left: 50%;
  top: 40px;
  transform: translateX(-50%);
  z-index: 5;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 6px;
  padding-right: 6px;
}

.navigation__list li {
  margin-left: 23px;
  margin-right: 23px;
  line-height: 15px;
}

.navigation__list li:first-child {
  margin-left: 0;
}

.navigation__list li:last-child {
  margin-right: 0;
}

.navigation__list li:nth-child(3) {
  margin-right: 170px;
}

.navigation__list li:nth-child(4) {
  margin-left: 170px;
}

.navigation__link {
  color: #000000;
  font-size: 15px;
  line-height: 15px;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.5s ease 0s;
}

.navigation__link:hover,
.navigation__link:focus {
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease 0s;
}

.navigation--da .main-header__link {
  color: #000000;
  font-size: 15px;
  line-height: 15px;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.5s ease 0s;
}

.navigation--da .main-header__link:hover,
.navigation--da .main-header__link:focus {
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.navigation--da .main-header__link:active {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease 0s;
}

.logo__link {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo span {
  text-transform: uppercase;
  font-family: "Bebas", "Arial", sans-serif;
  font-size: 8px;
  font-weight: 400;
  line-height: 8px;
  color: #000000;
}

.logo span:first-child {
  letter-spacing: 2.72px;
}

.logo span:last-child {
  letter-spacing: 8px;
}

.main-footer {
  background-color: #ffffff;
  padding-top: 99px;
  padding-bottom: 103px;
}

.main-footer__row {
  display: flex;
  justify-content: space-between;
}

.main-footer__col:first-child {
  display: flex;
  flex-direction: column;
}

.main-footer__col:first-child b {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2;
}

.main-footer__col:first-child p {
  font-style: italic;
  font-size: 16px;
  line-height: 1.2;
}

.main-footer__col:last-child {
  display: flex;
  justify-content: center;
}

.main-footer__col:last-child span {
  margin-right: 9px;
  margin-top: 9px;
  font-style: italic;
  font-size: 14px;
  line-height: 18px;
}

@media (min-width: 729.98px) {
  .navigation__link {
    position: relative;
  }

  .navigation__link::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 0;
    left: 0;
    transform: translateY(4px) scale(0);
  }

  .navigation__link:hover::after,
  .navigation__link:focus::after {
    transform: translateY(4px) scale(1, 1);
    transition: all 0.5s ease 0s;
  }

  .navigation__link:active::after {
    background-color: rgba(0, 0, 0, 0.3);
    transform: translateY(4px) scale(1, 1);
    transition: all 0.1s ease 0s;
  }

  .navigation--da .main-header__link {
    position: relative;
  }

  .navigation--da .main-header__link::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 0;
    left: 0;
    transform: translateY(4px) scale(0);
  }

  .navigation--da .main-header__link:hover::after,
  .navigation--da .main-header__link:focus::after {
    transform: translateY(4px) scale(1, 1);
    transition: all 0.5s ease 0s;
  }

  .navigation--da .main-header__link:active::after {
    background-color: rgba(0, 0, 0, 0.3);
    transform: translateY(4px) scale(1, 1);
    transition: all 0.1s ease 0s;
  }
}

@media (max-width: 849.98px) {
  .main-header.active::before {
    transition: all 0.3s ease 0s;
    background-color: #ffffff;
    z-index: 4;
  }

  .main-header__burger {
    display: block;
    width: 30px;
    height: 20px;
    z-index: 4;
    background-color: transparent;
    position: absolute;
    top: 34px;
    left: 24px;
    border: none;
    padding: 0;
    cursor: pointer;
  }

  .main-header__burger span {
    background-color: #000000;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger span:hover,
  .main-header__burger span:focus {
    outline: none;
  }

  .main-header__burger::before,
  .main-header__burger::after {
    content: "";
    background-color: #000000;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger::before {
    top: 0;
  }

  .main-header__burger::after {
    bottom: 0;
  }

  .main-header__burger.active span {
    transform: scale(0);
  }

  .main-header__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
    background-color: #000000;
  }

  .main-header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
    background-color: #000000;
  }

  .main-header__burger:hover,
  .main-header__burger:focus {
    outline: none;
  }

  .main-header__logo {
    top: 10px;
  }

  .navigation {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    transition: all 0.4s ease 0s;
    background-color: rgba(0, 0, 0, 0.2);
    padding: 160px 10px 20px 10px;
    z-index: 2;
  }

  .navigation.active {
    top: 0;
  }

  .navigation__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .navigation__list li {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .navigation__list li:nth-child(3) {
    margin-right: 0;
  }

  .navigation__list li:nth-child(4) {
    margin-left: 0;
  }

  .navigation__link {
    font-size: 25px;
    font-weight: 700;
    line-height: 1.4;
    color: #000000;
  }

  .navigation__link:hover,
  .navigation__link:focus {
    color: #000000;
  }

  .navigation__link:active {
    color: #000000;
    transition: all 0.1s ease 0s;
  }

  .navigation--da .main-header__btn {
    width: 40px;
    height: 40px;
    font-size: 25px;
  }

  .navigation--da .main-header__link {
    margin-bottom: 20px;
    color: #000000;
    font-size: 25px;
    line-height: 25px;
    text-decoration: none;
    transition: all 0.5s ease 0s;
  }

  .navigation--da .main-header__link:hover,
  .navigation--da .main-header__link:focus {
    color: rgba(0, 0, 0, 0.6);
    transition: all 0.5s ease 0s;
    outline: none;
  }

  .navigation--da .main-header__link:active {
    color: rgba(0, 0, 0, 0.3);
    transition: all 0.5s ease 0s;
  }
}

@media (max-width: 729.98px) {
  .main-footer {
    padding-top: 49px;
    padding-bottom: 43px;
  }

  .main-footer__row {
    flex-direction: column;
    align-items: center;
  }

  .main-footer__col:first-child b {
    margin-bottom: 8px;
  }

  .main-footer__col:first-child p {
    margin-bottom: 8px;
  }

  .main-footer__col:first-child {
    margin-bottom: 24px;
    text-align: center;
  }

  .main-footer__col:nth-child(2) {
    margin-bottom: 24px;
  }
}

@media (max-width: 449.98px) {
  .main-header__logo {
    transform: translateX(-40%);
  }

  .logo span {
    display: none;
  }
}

@media (max-width: 849.98px) and (min-width: 729.98px) {
  .navigation--da .main-header__link {
    position: relative;
  }

  .navigation--da .main-header__link::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 0;
    left: 0;
    transform: translateY(4px) scale(0);
  }

  .navigation--da .main-header__link:hover::after,
  .navigation--da .main-header__link:focus::after {
    transform: translateY(4px) scale(1, 1);
    transition: all 0.5s ease 0s;
  }

  .navigation--da .main-header__link:active::after {
    background-color: rgba(0, 0, 0, 0.3);
    transform: translateY(4px) scale(1, 1);
    transition: all 0.1s ease 0s;
  }
}